                    v-model="customer.email"
                  />
                  <div class="invalid-feedback">
                    {{ errors.first("register_form.Email") }}
                  </div>
                </div>
                <div class="form-group">
                  <div class="RadioButtonGroup8">
                    <div class="RadioButton8">
                      <input
                        type="radio"
