                      /><label for="hasAccount0">I'm already registered</label>
                    </div>
                    <div class="RadioButton8">
                      <input
                        type="radio"
                        id="hasAccount1"
                        name="hasAccount"
                        :value="false"
                        v-model="customerExist"
                      /><label for="hasAccount1"
