                      }"
                    />
                    <div class="invalid-feedback">
                      {{ errors.first("register_form.Firstname") }}
                    </div>
                  </div>
                  <div class="form-group">
                    <label>Last Name</label>
                    <input
                      class="form-control"
                      name="Lastname"
                      v-validate="'required'"
                      data-vv-scope="register_form"
                      type="text"
                      v-model="customer.lastname"
                      :class="{
