                      button-type="submit"
                      @btn-role="handleSubmit"
                      classes="btn btn-block btn-primary btn-jb jb-submit-btn"
                      :disabler="!customer.firstname || !customer.lastname"
                      activator="registering"
                    ></wolf-button>
                  </div>
                </template>
              </form>
            </div>
          </div>
