</template>

<script>
export default {
  name: "Login",
  props: {
    passwordReseted: {
      type: Boolean,
      default: false,
    },
    billingRequest: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    credentials: {
      email: null,
      password: null,
    },
    customer: {
      firstname: null,
      lastname: null,
      email: null,
      password: null,
    },
    invalidLogin: false,
    loginSucceed: false,
    isCheckOutRequest: false,
    emailSent: false,
    customerExist: true,
  }),
  created() {
    this.isCheckOutRequest =
      this.$route.query["origin-request"] &&
      this.$route.query["origin-request"] === "shopping-cart";
  },
  methods: {
    handleSubmit() {
      if (this.customerExist) this.handleLogin();
      else this.handleRegister();
    },
    handleLogin() {
      this.invalidLogin = false;
      this.loginSucceed = false;
      this.$validator
        .validateAll("signin_form", { initial: true })
        .then((result) => {
          if (result) {
            this.$http
              .post("auth/login", this.formData(this.credentials))
              .then((response) => {
                if (response.data.status) {
                  this.invalidLogin = false;
                  this.loginSucceed = true;
                  if (this.billingRequest || this.isCheckOutRequest)
                    window.location.replace("/shop/checkout");
                  else window.location.replace("/");
                }
              })
              .catch((err) => {
                if (err.response.status == 401) {
                  this.invalidLogin = true;
                  this.loginSucceed = false;
                }
              });
          }
        });
    },
    handleRegister() {
      this.$validator
        .validateAll("register_form", { initial: true })
        .then((result) => {
          if (result) {
            this.$http
              .post("auth/register", this.formData(this.customer))
              .then((response) => {
                if (!response.data.error) window.location.replace("/");
              });
          }
        });
    },
  },
};
</script>
