<script>
export default {
  name: "Cart",
  data: () => ({
    tickets: [],
    currentEvent: null,
    currentMatch: null,
    ticketModalOpen: false,
    selectedTicket: {},
  }),
  created() {
    this.$http
      .get(
        `ghost/tickets/list/${this.$route.query.service}/${this.$route.query.s_ref}`
      )
      .then((response) => {
        this.tickets = response.data.tickets;
        this.currentEvent = response.data.event;
        this.currentMatch = response.data.match;
      });
  },
  methods: {
    formatTime(time) {
      if (time) {
        let result = time.split(":");
        return result[0] + ":" + result[1];
      }
      return null;
    },
    formatMatchDay(match_date, returnDay = false) {
      let days = {
        1: "Mon",
        2: "Tue",
        3: "Wed",
        4: "Thu",
        5: "Fri",
        6: "Sat",
        0: "Sun",
      };
      if (returnDay) return days[new Date(match_date).getDay()];
      return new Date(match_date).getDate();
    },
    toggleTicketModal(ticket = null) {
      if (ticket) {
        this.selectedTicket = {
          title: ticket.title,
          qty: 1,
          reference: ticket.code,
          slug: ticket.slug,
        };
      } else {
        this.clearObject(this.selectedTicket);
      }
      this.ticketModalOpen = !this.ticketModalOpen;
      document.body.classList.toggle("modal-open");
    },
  },
};
</script>
