<script>
import LazyImage from "@/components/LazyImage.vue";
export default {
  name: "PostDetails",
  components: { LazyImage },
  data: () => ({
    post: {},
    related: [],
  }),
  beforeCreate() {
    this.$http
      .get(
        `ghost/news/${this.$route.params.category}/${this.$route.params.slug}`
      )
      .then((response) => {
        this.post = response.data.post;
        this.related = response.data.related;
      });
  },
  methods: {
    formatTime(_time) {
      return new Date(_time).toLocaleString("default", {
        hour: "2-digit",
        minute: "2-digit",
      });
    },
  },
};
</script>
