<script>
import { cartMixin } from "@/_helpers/cart-mixins.js";
export default {
  name: "ShoppingCart",
  mixins: [cartMixin],
  data: () => ({
    cartProducts: [],
    totalCartAmount: 0,
    grandTotal: 0,
    discount: 0,
  }),
  created() {
    this.cartProducts = this.parseItems();
    this.getCartTotalAmount();
    this.calculateDiscount();
  },
  methods: {
    getCartTotalAmount() {
      this.totalCartAmount = this.cartTotal();
    },
    clearCart() {
      this.$store.dispatch("clearCart").then(() => {
        this.$root.$emit("newCartItemsListner", this.afteraCart());
        this.$router.push({ name: "home" });
      });
    },
    removeCartItem: function () {
      this.$store.dispatch("cartRemove", this.choosenRow).then(() => {
        this.cartProducts = this.parseItems();
        this.getTotalItems();
        this.getCartTotalAmount();
        this.calculateDiscount();
        this.$root.$emit("newCartItemsListner", this.afteraCart());
      });
    },
    startCheckout() {
      if (this.$store.getters.isLoggedIn)
        this.$router.push({ name: "shopCheckout" });
      else
        this.$router.push({
          name: "PublicLogin",
          params: { billingRequest: true },
          query: {
            "origin-request": "shopping-cart",
            "redirect-url":
              window.location.origin +
              "/en-" +
              this.currentCountryCode +
              "/check-out-process",
          },
        });
    },
    toggleActions(action) {
      switch (action) {
        case "products_removal":
          this.clearCart();
          break;
        case "product_delete":
          this.removeCartItem();
          break;
        default:
          console.log("default data");
          break;
      }
      this.alertDefaults.modalOpen = false;
    },
    productsRemove() {
      this.alertDefaults = {
        title: "Clearing cart",
        content:
          "Are you sure you want to remove all products from shopping cart?",
        actionType: "products_removal",
        actionButton: "Yes, clear",
        classes: "btn btn-danger",
        modalOpen: true,
      };
    },
    productDelete(title) {
      this.alertDefaults = {
        title: "Removing cart item",
        content:
          "Are you sure you want to remove this product fro shopping cart: <b>" +
          title +
          "</b>",
        actionType: "product_delete",
        actionButton: "Yes, remove",
        classes: "btn btn-danger",
        modalOpen: true,
      };
    },
    updateProductQty(id, indexx) {
      var updateValue = Number(
        document.getElementById("qty" + indexx).innerText
      );
      if (updateValue) {
        let cart = { id: id, qty: updateValue };
        this.$store.dispatch("cartQtyRequest", cart).then(() => {
          this.$root.$emit("newCartItemsListner", this.afteraCart());
          this.getCartTotalAmount();
          this.calculateDiscount();
        });
      }
    },
    calculateDiscount() {
      let cartProducts = this.parseItems();
      let grandTotal = 0;
      cartProducts.forEach((product) => {
        grandTotal += Number(this.getProdutSubTotal(product));
      });
      this.grandTotal = grandTotal;
    },
    getDiscount(product) {
      return (
        this.getProdutSubTotal(product) -
        this.getCartDiscount(product).replace(/,/g, "")
      );
    },
    displayOptions(givenOptions) {
      return Object.values(givenOptions).join(" | ");
    },
  },
};
</script>
