<script>
import LazyImage from "@/components/LazyImage.vue";
export default {
  name: "Shop",
  components: { LazyImage },
  data: () => ({
    products: [],
    categories: [],
    priceRange: [0, 100],
    selectedBrands: [],
    minPrice: 0,
    maxPrice: 100000,
    sortOptions: [
      { value: "5", label: "Recommended" },
      { value: "8", label: "Price Low - High" },
      { value: "9", label: "Price High - Low" },
      { value: "6", label: "Highest Discount" },
    ],
  }),
  created() {
    let url =
      this.$route.params.category_slug !== undefined
        ? "ghost/shop/products/" + this.$route.params.category_slug
        : "ghost/shop/products";
    this.$http.get(url).then((response) => {
      this.products = response.data.products;
      this.categories = response.data.categories;
    });
  },
  computed: {},
  updated() {
    document.querySelectorAll("div.filter").forEach((elt) => {
      elt.addEventListener("click", () => {
        elt.classList.toggle("active-filter");
      });
    });
  },
  methods: {
    toggleCategories() {
      document
        .getElementById("categories-sidebar")
        .classList.toggle("categories-open");
    },
    priceFilter() {
      this.$router.push({
        query: {
          "min-price": this.priceRange[0],
          "max-price": this.priceRange[1],
        },
      });
      console.log(this.priceRange);
    },
    handleCategoryFilter(category_slug) {
      this.$router.push({
        name: "",
        query: { "category-slug": category_slug },
      });
    },
    getLandindData() {
      this.$http.get("market/shop-landing-data")
        .then((response) => {
          this.products = response.data.products;
        });
    },
  },
};
</script>
