<template>
  <div class="bg-white py-3">
    <div class="container" v-if="cards.length">
      <div class="row mb-5">
        <div class="col-lg-12">
          <h4 class="mb-0">MEMBERSHIPS CARDS</h4>
          <p>Request Your Membership Card</p>
        </div>
      </div>
      <div class="row">
        <div
          class="col-xxl-2 col-lg-3 col-sm-4 mb-3"
          v-for="(card, i) in cards"
          :key="'card-for' + i"
        >
          <a
            :href="
              $router.resolve({
                name: 'servicesCheckout',
                query: {
                  service: 'membership',
                  s_ref: card.code,
                  slug: card.slug,
                },
              }).href
            "
            class="card shadow"
          >
            <img
              :src="`${apiURL}/${card.example_card}`"
              class="card-img-top"
              :alt="card.title"
              style="height: 175px"
            />
            <div class="card-inner">
              <h5 class="card-title text-upper">{{ card.title }}</h5>
              <div class="card-text">
                <p
                  class="modalidad_descuento"
                  :class="{ invisible: !card.promotionalprice }"
                >
                  <span class="dinero euro">RWF {{ addComma(card.price) }}</span
                  >/{{ card.expiration_period }} {{ card.expiration_unit }}
                </p>
                <p class="modalidad_cantidad">
                  <b></b>
                  <span class="dinero euro">
                    RWF
                    {{
                      addComma(card.promotionalprice) || addComma(card.price)
                    }}</span
                  >/
                  <span class="modalidad_primer">
                    {{ card.expiration_period }} {{ card.expiration_unit }}
                  </span>
                </p>
              </div>
              <button
                type="button"
                class="btn btn-primary btn-lg btn-block mt-3"
              >
                <div class="d-flex align-items-center">
                  <span>Subscribe Now</span>
                </div>
              </button>
            </div>
          </a>
        </div>
      </div>
      <div class="row modalidades_container" v-if="false">
        <div
          class="col-xxl-2 col-lg-3 mb-3"
          v-for="(card, i) in cards"
          :key="'card-for' + i"
        >
          <input
            type="radio"
            :value="card.id"
            :name="'card' + i"
            :id="'card' + i"
            v-model="currentCard"
          />
          <label :for="'card' + i">
            <p class="modalidad_madridista">Click to select</p>
            <p class="modalidad_tipo">{{ card.title }}</p>
            <p class="modalidad_menores">&nbsp;</p>
          </label>
        </div>
        <div class="col-lg-12" v-if="purchaseLink">
          <div class="text-center">
            <a
              class="hyper-button hyper-button--wide ng-star-inserted"
              :href="purchaseLink"
            >
              Subscribe Now
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Cards",
  data: () => ({
    cards: [],
    isMobo: false,
    currentCard: null,
    purchaseLink: null,
  }),
  watch: {
    currentCard() {
      let card = this.cards.find(({ id }) => id === this.currentCard);
      this.purchaseLink = this.$router.resolve({
        name: "servicesCheckout",
        query: {
          service: "membership",
          s_ref: card.code,
          slug: card.slug,
        },
      }).href;
    },
  },
  beforeCreate() {
    this.$http.get("ghost/membership-cards")
      .then((response) => {
        this.cards = response.data.cards;
      });
  },
  created() {
    this.checkMobileEnv();
  },

  methods: {
    checkMobileEnv() {
      if (matchMedia) {
        const mq = window.matchMedia("(max-width: 768px)");
        this.isMobo = mq.matches;
        try {
          // Chrome & Firefox
          mq.addEventListener("change", () => {
            this.isMobo = mq.matches;
          });
        } catch (e1) {
          try {
            // Safari
            mq.addListener(() => {
              this.isMobo = mq.matches;
            });
          } catch (e2) {
            console.error(e2);
          }
        }
        //this.WidthChange(mq);
      }
    },
    WidthChange(mq) {
      this.isMobo = mq.matches;
    },
  },
};
</script>
