<script>
import Slick from "vue-slick";
import SizeGuide from "./SizeGuide.vue";
import { cartMixin } from "@/_helpers/cart-mixins.js";
export default {
  name: "ProductSingle",
  mixins: [cartMixin],
  components: {
    Slick,
    SizeGuide,
  },
  cartItems: function () {
    return this.$store.getters.cartItems;
  },
  data: () => ({
    product: {},
    similarProducts: [],
    steps: [],
    currentOptions: {
      color: null,
      size: null,
    },
    ProductsOptions: {
      slidesToShow: 1,
      dots: true,
      slidesToScroll: 1,
      arrows: true,
      prevArrow:
        '<button type="button" class="slick-prev"><i class="ti-arrow-left"></i></button>',
      nextArrow:
        '<button type="button" class="slick-next"><i class="ti-arrow-right"></i></button>',
    },
    newCart: {
      quantity: 1,
    },
    qtyError: false,
  }),
  created() {
    this.$http
      .post(
        "ghost/shop/products",
        this.formData({ slug: this.$route.params.slug })
      )
      .then((response) => {
        this.product = response.data.product;
        this.currentOptions.color = response.data.product.color_name;
        this.ProductsOptions.customPaging = (slick, index) => {
          var imgSrc = slick.$slides.eq(index).find("img").attr("src");
          return (
            '<a href="javascript:void(0)"><img src=" ' + imgSrc + ' "></a></li>'
          );
        };
        this.$nextTick(() => {
          this.$refs.ProductSlick.reSlick();
          document
            .getElementsByClassName("slick-dots")[0]
            .classList.add("product-thumbnails");
        });
      });
  },
  methods: {
    getSelectedProduct(slug) {
      this.$http
        .post("ghost/shop/products", this.formData({ slug: slug }))
        .then((response) => {
          this.product = response.data.product;
          this.currentOptions.color = response.data.product.color_name;
          this.ProductsOptions.customPaging = (slick, index) => {
            var imgSrc = slick.$slides.eq(index).find("img").attr("src");
            return (
              '<a href="javascript:void(0)"><img src=" ' +
              imgSrc +
              ' "></a></li>'
            );
          };
          this.$nextTick(() => {
            this.$refs.ProductSlick.reSlick();
            document
              .getElementsByClassName("slick-dots")[0]
              .classList.add("product-thumbnails");
          });
        });
    },
    parseItems() {
      let data = localStorage.getItem("cartItems");
      return data ? JSON.parse(data) : [];
    },
    afteraCart() {
      return {
        items: this.cartItems.reduce((prev, cur) => prev + Number(cur.qty), 0),
        productsArr: this.parseItems(),
      };
    },
    addToCart(product) {
      if (this.newCart.quantity) {
        this.qtyError = false;
        let cart = {
          row_id: this.uuidv4(),
          id: product.id,
          price: product.regular_price,
          retail_percentage: product.discount_percentage || 0,
          retail_price: product.sale_price || 0,
          title: product.title,
          image: product.main_image,
          qty: this.newCart.quantity,
          ref_key: product.user_id,
          options: this.currentOptions,
          isLoan: false,
        };
        this.$store.dispatch("cartRequest", cart).then(() => {
          this.$root.$emit("newCartItemsListner", this.afteraCart());
        });
      } else {
        this.qtyError = true;
      }
    },
  },
};
</script>
