<script>
import { cartMixin } from "@/_helpers/cart-mixins.js";
import countries from "@/_helpers/countries.json";
export default {
  name: "CheckOutProcess",
  mixins: [cartMixin],
  data: () => ({
    newShipping: {
      firstname: null,
      lastname: null,
      email: null,
      phone_number: null,
      comments: null,
      momo_number: null,
      terms_agreed: 1,
      address: {
        address_one: null,
        address_two: null,
        country: "Rwanda",
        city: null,
        zip_code: null,
        state: null,
      },
    },
    cartProducts: [],
    totalCartAmount: 0,
    grandTotal: 0,
    discount: 0,
    countries: countries,
    paymentMethod: "momo",
    processing: false,
    errorsMessages: {},
    paymentMeta: {},
    paymentModal: false,
    paymentStatusInterval: null,
    paymentCancelled: false,
  }),
  computed: {
    validationErrors() {
      let errors = Object.values(this.errorsMessages);
      errors = errors.flat();
      return errors;
    },
    btnDisabled() {
      return (
        this.$store.state.requestFlag == "placingOrder" ||
        !this.newShipping.firstname ||
        !this.newShipping.phone_number
      );
    },
  },
  beforeMount() {
    if (this.isUserLoggedIn) {
      if (this.isUserLoggedIn) {
        this.$http.get("auth/me").then((response) => {
          this.$store.dispatch("setCurrentUser", { ...response.data.user });
          let user = response.data.user;
          this.newShipping.firstname = user.first_name;
          this.newShipping.lastname = user.last_name;
          this.newShipping.phone_number = user.phone;
          this.newShipping.email = user.email;
          this.newShipping.user_id = user.id;
        });
      }
    }
  },
  created() {
    this.cartProducts = this.parseItems();
    this.getCartTotalAmount();
    this.calculateDiscount();
  },
  methods: {
    getDefaultNumber() {
      if (!this.newShipping.momo_number) {
        if (this.newShipping.phone_number) {
          this.newShipping.momo_number = this.newShipping.phone_number;
        }
      }
    },
    delegatePaymentAction() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          let newForm = {
            cartProducts: this.cartProducts,
            payment_method: this.paymentMethod,
            grand_total: this.grandTotal,
          };

          this.$store.state.requestFlag = "placingOrder";
          this.processing = true;
          this.paymentModal = false;
          this.paymentCancelled = false;
          this.$http
            .post(
              "ghost/shop/orders/create",
              this.formData({ ...newForm, ...this.newShipping })
            )
            .then((response) => {
              if (response.data.status) {
                this.paymentMeta = response.data.payment;
                this.processing = false;
                if (this.paymentMeta.success == 1) {
                  if (
                    this.paymentMethod == "momo" ||
                    this.paymentMethod == "spenn"
                  ) {
                    if (this.paymentMeta.reply == "PENDING") {
                      this.handlePendingPayment({
                        transaction_ref: this.paymentMeta.refid,
                        authkey: this.paymentMeta.authkey,
                        tid: this.paymentMeta.tid,
                      });
                    }
                    this.paymentModal = true;
                  } else {
                    window.location.replace(response.data.payment.url);
                  }
                } else {
                  this.paymentModal = true;
                  this.paymentCancelled = true;
                }
              } else {
                this.errorsMessages = response.data.message;
              }
            });
        }
      });
    },
    handlePendingPayment(obj) {
      if (this.paymentStatusInterval) {
        clearInterval(this.paymentStatusInterval);
      }
      this.$store.state.requestFlag = "fetchingStatus";
      let url = "payment-status";
      let formData = this.formData(obj);

      this.$http
        .post(url, formData)
        .then((response) => {
          if (response.data.transaction) {
            var status = response.data.transaction.status;
            if (status != "successful" && status != "failed") {
              this.paymentStatusInterval = setInterval(() => {
                this.$store.state.requestFlag = "fetchingStatus";
                this.$http.post(url, formData).then((response) => {
                  if (response.data.transaction.status == "successful") {
                    clearInterval(this.paymentStatusInterval);
                    this.$router.replace({
                      name: "PublicDownloads",
                      params: {},
                      query: { origin: "payment", thankyou: true },
                    });
                  }
                  if (response.data.transaction.status == "failed") {
                    clearInterval(this.paymentStatusInterval);
                    this.paymentCancelled = true;
                  }
                });
              }, 4000);
            }
          }
        })
        .catch(() => {
          this.paymentCancelled = true;
        });
    },
    handlePaymentToggler(payment) {
      this.paymentMethod = payment;
    },
    getCartTotalAmount() {
      this.totalCartAmount = this.cartTotal();
    },
    displayOptions(givenOptions) {
      return Object.values(givenOptions).join(" | ");
    },
    placeOrder() {
      let newForm = {
        cartItems: this.cartItems,
      };
      this.$http
        .post(
          "shop/orders/create",
          this.formData({ ...newForm, ...this.newShipping })
        )
        .then((response) => {
          this.$store.dispatch("clearCart").then(() => {
            window.location.replace(
              `/shop/thankyou?order-code=${response.data.order_code}`
            );
          });
        });
    },
    calculateDiscount() {
      let cartProducts = this.parseItems();
      let grandTotal = 0;
      cartProducts.forEach((product) => {
        grandTotal += Number(this.getProdutSubTotal(product));
      });
      this.grandTotal = grandTotal;
    },
  },
};
</script>
