<template>
  <main>
    <banner v-if="banners.length" :banners="banners"></banner>
    <results-belt
      :results="[...results, ...matches]"
      v-if="banners.length"
    ></results-belt>
    <ads></ads>
    <div class="bg-white pt-2">
      <featured-videos v-if="videos.length" :videos="videos"></featured-videos>
    </div>
    <featured-news v-if="posts.length" :posts="posts"></featured-news>
    <div class="bg-secondary pt-3">
      <fixtures :matches="matches" :products="products"></fixtures>
    </div>
    <players></players>
    <div class="container py-5" v-if="galleries.length">
      <div class="row">
        <div class="col-lg-12 mb-4">
          <h2>PHOTO GALLERIES</h2>
        </div>
        <div
          class="col-lg-4"
          v-for="(item, i) in galleries"
          :key="'hallery-item' + i"
        >
          <div class="fcb-row content">
            <div class="fcb-gr-4 fcb-gr-12@medium fcb-gr-12@small w-100">
              <router-link
                :to="{ name: 'GalleryItems', params: { slug: item.slug } }"
                title="View gallery"
                class="text-teaser v1"
              >
                <div class="teaser-image-wrapper">
                  <div class="image-wrapper loaded">
                    <img
                      :src="apiURL + '/' + item.main_image"
                      style="width: 100%; height: auto"
                    />
                  </div>

                  <span class="teaser-type type-gallery">
                    <span class="icon icon-gallery type-gallery">
                      <svg viewBox="0 0 26 21" preserveAspectRatio="xMidYMid">
                        <path
                          d="M23.8 6.5L23.7 8l.7.1-1.1 11.3-18.5-1.8-.1 1.5L24.6 21 26 6.7l-2.2-.2zM20 0H0v14.3h20V0zm-1.4 12.9H1.5V1.5h17.1v11.4zm4.6-9.4l-2.4-.2v1.5l.8.1-.7 11.4-18.5-1.2-.1 1.4 20 1.3.9-14.3z"
                        ></path>
                      </svg>
                    </span>
                  </span>
                </div>

                <div class="text-content">
                  <span class="teaser-badges"> </span>

                  <div class="">Gallery</div>
                  <div class="h6">
                    {{ item.title }}
                  </div>
                  <p class="two-lines">
                    {{ item.summary }}
                  </p>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bg-secondary">
      <events></events>
    </div>
  </main>
</template>
<script>
import Banner from "./Banner.vue";
import FeaturedVideos from "./FeaturedVideos.vue";
import FeaturedNews from "./FeaturedNews.vue";
import Fixtures from "./Fixtures.vue";
import ResultsBelt from "./ResultsBelt.vue";
import Ads from "./Ads.vue";
import Events from "./Events.vue";
import Players from "./Players.vue";
export default {
  name: "Landing",
  components: {
    Banner,
    FeaturedVideos,
    FeaturedNews,
    Fixtures,
    ResultsBelt,
    Ads,
    Events,
    Players,
  },
  data: () => ({
    banners: [],
    videos: [],
    posts: [],
    fixtures: [],
    galleries: [],
    matches: [],
    results: [],
    products: [],
  }),
  created() {
    this.getLandindData();
  },
  methods: {
    getLandindData() {
      this.$http.get("ghost/home").then((response) => {
        this.banners = response.data.banners;
        this.videos = response.data.videos;
        this.posts = response.data.posts;
        this.fixtures = response.data.fixtures;
        this.galleries = response.data.galleries;
        this.matches = response.data.matches;
        this.results = response.data.results;
        this.products = response.data.products;
      });
    },
  },
};
</script>
<style lang="css">
.ep {
  width: 75%;
}
.offer-banner:first-child {
  padding-right: 0 !important;
}
.offer-banner:last-child {
  padding-left: 0 !important;
}
</style>
