<script>
export default {
  name: "Tickets",
  data: () => ({
    matches: []
  }),
  beforeCreate() {
    this.$http.get("ghost/match-tickets")
      .then((response) => {
        this.matches = response.data.matches;
      });
  },
  methods: {
    formatTime(time) {
      if (time) {
        let result = time.split(":");
        return result[0] + ":" + result[1];
      }
      return null;
    },
    formatMatchDay(match_date, returnDay = false) {
      let days = {
        1: "Monday",
        2: "Tuesday",
        3: "Wednesday",
        4: "Thursday",
        5: "Friday",
        6: "Saturday",
        0: "Sunday",
      };
      if (returnDay) return days[new Date(match_date).getDay()];
      let result = new Date(match_date).getDate();
      return result >= 10 ? result : "0" + result;
    },
    getMatchMonth(match_date) {
      return new Date(match_date).toLocaleString("default", { month: "long" });
    },
  },
};
</script>
