<template>
  <div class="bg-squares py-4">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-lg-10">
          <div class="matches">
            <div class="ng-star-inserted">
              <div
                class="matches__month"
                v-for="(item, k) in fixtures"
                :key="'item' + k"
              >
                <h3 class="matches__month__title">{{ item.match_day }}</h3>
                <div class="content">
                  <article
                    class="match-card match-card--wide py-4"
                    v-for="(match, i) in item.matches"
                    :key="'match' + k + i"
                  >
                    <div>
                      <ul class="match-card__where">
                        <li class="match-card__where__competition">
                          <span class="competition-name">{{
                            match.competition || "N/A"
                          }}</span>
                        </li>
                        <li class="match-card__where__venue">
                          {{ match.venue || "Not Stadium Yet" }}
                        </li>
                        <li class="match-card__where__date">
                          <span v-if="match.match_date">{{
                            chatDate(match.match_date)
                          }}</span>
                          <span v-else>N/A</span>,
                          <span>{{
                            formatTime(match.match_time) || "N/A"
                          }}</span>
                        </li>
                      </ul>
                      <div class="match-card__fixture">
                        <div class="match-card__fixture__home">
                          <template v-if="match.is_home == 1">
                            <div class="badge">
                              <img
                                :src="appLogo"
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                            </div>
                            <span class="team-name">{{
                              appSettings.club_name
                            }}</span>
                          </template>
                          <template v-else>
                            <div class="badge">
                              <img
                                alt=""
                                :src="apiURL + '/' + match.opponent_logo"
                                v-if="match.opponent_logo"
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                              <img
                                :src="`${publicPath}img/club-logo-placeholder.png`"
                                v-else
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                            </div>
                            <span class="team-name">{{
                              match.opponent_name
                            }}</span>
                          </template>
                          <span class="team__vs" v-if="false">VS</span>
                        </div>
                        <div class="match-card__fixture__away">
                          <template v-if="match.is_home == 1">
                            <div class="badge">
                              <img
                                alt=""
                                :src="apiURL + '/' + match.opponent_logo"
                                v-if="match.opponent_logo"
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                              <img
                                :src="`${publicPath}img/club-logo-placeholder.png`"
                                v-else
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                            </div>
                            <span class="team-name">{{
                              match.opponent_name
                            }}</span>
                          </template>
                          <template v-else>
                            <div class="badge">
                              <img
                                :src="appLogo"
                                class="ng-star-inserted"
                                style="opacity: 1"
                              />
                            </div>
                            <span class="team-name">{{
                              appSettings.club_name
                            }}</span>
                          </template>
                        </div>
                        <div class="match-card__fixture__when">
                          <div class="match-card__fixture__when__date">
                            <span
                              class="match-card__fixture__when__date__day"
                              >{{ formatMatchDay(match.match_date) }}</span
                            >
                            <span
                              class="match-card__fixture__when__date__dayofweek"
                            >
                              {{ formatMatchDay(match.match_date, true) }}</span
                            >
                          </div>
                        </div>
                      </div>
                      <footer class="match-card__links">
                        <div class="match-card__broadcaster">
                          <img
                            alt=""
                            src=""
                            v-if="false"
                            class="ng-star-inserted"
                            style="opacity: 1"
                          />
                        </div>
                        <div class="match-card__cta">
                          <router-link
                            class="btn btn-sm btn-primary rounded"
                            :to="{
                              name: 'TicketsCart',
                              query: { service: 'fixture', s_ref: match.uuid },
                            }"
                            >BUY TICKETS</router-link
                          >
                        </div>
                      </footer>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Fixtures",
  data: () => ({
    fixtures: [],
  }),
  beforeCreate() {
    this.$http.get("ghost/fixtures").then((response) => {
      this.fixtures = response.data.fixtures;
    });
  },
  methods: {
    formatTime(time) {
      if (time) {
        let result = time.split(":");
        return result[0] + ":" + result[1];
      }
      return null;
    },
    formatMatchDay(match_date, returnDay = false) {
      let days = {
        1: "Mon",
        2: "Tue",
        3: "Wed",
        4: "Thu",
        5: "Fri",
        6: "Sat",
        0: "Sun",
      };
      if (returnDay) return days[new Date(match_date).getDay()];
      return new Date(match_date).getDate();
    },
  },
};
</script>
