<template>
  <div class="page-content" id="listings-wrapper" v-if="!empty(row)">
    <div class="container mt-2">
      <h2>Gallery</h2>
      <div class="w-100">
        <h4 class="mb-0">{{ row.title }} - {{ row.category_name }}</h4>
        <h6>{{ photos.length }} images</h6>
      </div>
      <div class="products-listing">
        <div class="listpage-content mb-5">
          <div
            class="listpage-items clearfix padding-edges magic-warpper-parent"
          >
            <div class="magic-warpper" id="magic-warpper">
              <div
                class="mosaic-asset sale"
                :style="`width:${
                  (item.width * 250) / item.height
                }px;flex-grow:${(item.width * 250) / item.height}`"
                v-for="(item, i) in photos"
                :key="'photo' + i"
              >
                <figure>
                  <lazy-image
                    :class="`cls-img-only magic-item`"
                    :src="`${apiURL}/${item.image}`"
                    :height="item.height"
                    :width="item.width"
                    :alt="row.title"
                  />
                  <figcaption class="figcaption-overlay w-100"></figcaption>
                </figure>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class="d-flex-w-100 justify-content-center text-center my-5"
        v-if="isRequesting && false"
      >
        <div class="spinner-border text-warning" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LazyImage from "@/components/LazyImage.vue";
export default {
  name: "GalleryItems",
  components: { LazyImage },
  data: () => ({
    row: {},
    photos: [],
    isRequesting: true,
  }),
  beforeCreate() {
    this.$http.get(`ghost/home/galleries/${this.$route.params.slug}`)
      .then((response) => {
        this.photos = response.data.photos;
        this.row = response.data.row;
      });
  },
};
</script>

