<template>
  <div class="container" v-if="hasHads">
    <div class="row mt-2">
      <div class="col-lg-4" v-for="(item, i) in ads.half" :key="'ads-half' + i">
        <a :href="item.url">
          <img :src="`${apiURL}/${item.image}`" alt="" />
        </a>
      </div>
      <div class="col-lg-12 mt-2">
        <a :href="ads.full[1].url">
          <img :src="`${apiURL}/${ads.full[1].image}`" alt="" />
        </a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Ads",
  data: () => ({
    ads: [],
  }),
  computed: {
    hasHads() {
      return !this.empty(this.ads);
    },
  },
  beforeCreate() {
    this.$http.get("ghost/home/ads").then((response) => {
      this.ads = response.data.ads;
      this.$store.commit("SET_ADS", response.data.ads);
    });
  },
};
</script>
